.hero {
  height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #0a0a0a;
}

.hero-collage {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 2px;
}

.hero-collage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(0.4) contrast(1.1) grayscale(30%);
  opacity: 0.8;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.hero-collage img:hover {
  filter: brightness(0.7) contrast(1.2) grayscale(0%);
  opacity: 0.9;
  transform: scale(1.05);
  z-index: 1;
}

.hero::after {
  content: '';
  position: absolute;
  inset: 0;
  background: 
    radial-gradient(circle at center, transparent 0%, rgba(10, 10, 10, 0.7) 100%),
    linear-gradient(180deg, rgba(10, 10, 10, 0.4) 0%, rgba(10, 10, 10, 0.8) 100%);
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.hero-badge {
  display: inline-block;
  padding: 10px 28px;
  background: linear-gradient(135deg, #dc143c, #ff1744);
  color: #ffffff;
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 4px;
  border-radius: 0;
  margin-bottom: 30px;
  text-transform: uppercase;
  font-family: 'Space Grotesk', sans-serif;
  position: relative;
  overflow: hidden;
  animation: slideInDown 0.8s ease-out;
}

.hero-badge::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  animation: shimmer 3s infinite;
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shimmer {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

.hero-title {
  font-size: clamp(4rem, 12vw, 9rem);
  letter-spacing: 8px;
  position: relative;
  display: inline-block;
  color: #ffffff;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 700;
  margin: 20px 0;
  text-transform: uppercase;
  animation: fadeInUp 1s ease-out 0.2s both;
  text-shadow: 0 0 40px rgba(220, 20, 60, 0.5);
}

.hero-title span:first-child {
  position: relative;
  z-index: 1;
}

.hero-title-blur {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #dc143c;
  filter: blur(20px);
  opacity: 0.4;
  z-index: 0;
  animation: pulseGlow 3s ease-in-out infinite;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulseGlow {
  0%, 100% {
    opacity: 0.3;
    filter: blur(20px);
  }
  50% {
    opacity: 0.6;
    filter: blur(25px);
  }
}

.hero-subtitle {
  margin-top: 30px;
  font-size: clamp(1rem, 2vw, 1.5rem);
  opacity: 0.95;
  letter-spacing: 3px;
  font-weight: 300;
  color: #ffffff;
  font-family: 'Inter', sans-serif;
  animation: fadeInUp 1s ease-out 0.4s both;
}

.hero-description {
  margin-top: 15px;
  font-size: clamp(0.85rem, 1.3vw, 1.1rem);
  opacity: 0.85;
  letter-spacing: 2px;
  font-weight: 400;
  color: #ffd700;
  font-family: 'Inter', sans-serif;
  animation: fadeInUp 1s ease-out 0.6s both;
}

.hero-ticket-btn {
  display: inline-flex;
  align-items: center;
  gap: 15px;
  margin-top: 50px;
  padding: 18px 60px;
  background: linear-gradient(135deg, #dc143c, #ff1744);
  border: 2px solid #ffffff;
  color: #ffffff;
  letter-spacing: 3px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 700;
  font-size: 1rem;
  border-radius: 0;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  font-family: 'Space Grotesk', sans-serif;
  text-transform: uppercase;
  animation: fadeInUp 1s ease-out 0.8s both;
}

.hero-ticket-btn::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  transform: translate(-50%, -50%);
  transition: width 0.5s, height 0.5s;
}

.hero-ticket-btn:hover::before {
  width: 400px;
  height: 400px;
}

.hero-ticket-btn:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 40px rgba(220, 20, 60, 0.6);
  background: linear-gradient(135deg, #ff1744, #ff4569);
  border-color: #ffd700;
}

.hero-ticket-btn span,
.hero-ticket-btn i {
  position: relative;
  z-index: 1;
}

.hero-ticket-btn i {
  transition: transform 0.3s;
}

.hero-ticket-btn:hover i {
  transform: translateX(8px);
}

.hero-ticketing-partner {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  animation: fadeInUp 1s ease-out 1s both;
}

.ticketing-badge {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  font-size: 0.85rem;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: 'Inter', sans-serif;
  transition: all 0.3s;
}

.ticketing-badge:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: #ffd700;
  transform: translateY(-2px);
}

.ticketing-badge i {
  color: #ffd700;
  font-size: 1rem;
}

.ticketing-link {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.8);
  font-family: 'Inter', sans-serif;
  letter-spacing: 1px;
}

.ticketing-link span {
  opacity: 0.7;
}

.zoniq-link {
  color: #ffd700;
  text-decoration: none;
  font-weight: 700;
  transition: all 0.3s;
  font-family: 'Space Grotesk', sans-serif;
  font-size: 1.1rem;
  position: relative;
  padding: 0 5px;
}

.zoniq-link::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #dc143c, #ffd700);
  transition: width 0.3s;
}

.zoniq-link:hover {
  color: #ffffff;
  text-shadow: 0 0 15px rgba(255, 215, 0, 0.6);
}

.zoniq-link:hover::after {
  width: 100%;
}
