.artist-stats {
  padding: 100px 20px;
  background: #0a0a0a;
  position: relative;
  overflow: hidden;
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.artist-stats::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 30%, rgba(220, 20, 60, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(255, 215, 0, 0.08) 0%, transparent 50%);
  pointer-events: none;
  animation: backgroundFloat 10s ease-in-out infinite;
}

@keyframes backgroundFloat {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

.artist-stats.is-visible {
  opacity: 1;
  transform: translateY(0);
}

.stats-container {
  max-width: 1400px;
  margin: 0 auto;
}

.stats-header {
  text-align: center;
  margin-bottom: 70px;
}

.stats-title {
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 700;
  color: #ffffff;
  letter-spacing: 6px;
  margin-bottom: 20px;
  text-transform: uppercase;
  position: relative;
  display: inline-block;
}

.stats-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #dc143c, #ffd700, #dc143c, transparent);
  animation: lineExpand 1s ease-out;
}

@keyframes lineExpand {
  from {
    width: 0;
  }
  to {
    width: 80px;
  }
}

.stats-subtitle {
  font-size: clamp(0.9rem, 1.3vw, 1.1rem);
  color: #ffffff;
  letter-spacing: 3px;
  opacity: 0.8;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
  text-transform: uppercase;
  margin-top: 20px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
  gap: 30px;
  margin-top: 60px;
}

.stat-card {
  background: rgba(20, 20, 20, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0;
  padding: 40px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(220, 20, 60, 0.1), transparent);
  transition: left 0.6s;
}

.stat-card:hover::before {
  left: 100%;
}

.stat-card:hover {
  transform: translateY(-8px);
  border-color: rgba(220, 20, 60, 0.5);
  box-shadow: 0 15px 50px rgba(220, 20, 60, 0.3);
  background: rgba(25, 25, 25, 0.9);
}

.stat-card-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-card-header i {
  font-size: 2.2rem;
  filter: drop-shadow(0 0 8px currentColor);
  transition: transform 0.3s;
}

.stat-card:hover .stat-card-header i {
  transform: scale(1.1) rotate(5deg);
}

.stat-platform {
  font-size: 1.6rem;
  font-weight: 700;
  letter-spacing: 3px;
  color: #ffffff;
  font-family: 'Space Grotesk', sans-serif;
  text-transform: uppercase;
}

.stat-content {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.stat-main-metric {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.stat-label {
  font-size: 0.8rem;
  color: #ffd700;
  letter-spacing: 2px;
  font-weight: 600;
  text-transform: uppercase;
  opacity: 0.9;
  font-family: 'Inter', sans-serif;
}

.stat-value {
  font-size: 2rem;
  font-weight: 800;
  color: #ffffff;
  font-family: 'Space Grotesk', sans-serif;
  margin-bottom: 15px;
  text-shadow: 0 0 20px rgba(220, 20, 60, 0.3);
}

.stat-details {
  display: flex;
  flex-direction: column;
  gap: 18px;
  margin-top: 10px;
}

.stat-detail-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 0;
  border-left: 3px solid rgba(220, 20, 60, 0.6);
  transition: all 0.3s;
}

.stat-detail-item:hover {
  background: rgba(220, 20, 60, 0.1);
  border-left-color: #dc143c;
  transform: translateX(5px);
}

.stat-detail-item.insight {
  background: rgba(255, 215, 0, 0.05);
  border-left-color: #ffd700;
}

.stat-detail-item.insight:hover {
  background: rgba(255, 215, 0, 0.1);
}

.detail-label {
  font-size: 0.7rem;
  color: #ffd700;
  letter-spacing: 1.5px;
  font-weight: 600;
  text-transform: uppercase;
  opacity: 0.9;
  font-family: 'Inter', sans-serif;
}

.detail-value {
  font-size: 0.9rem;
  color: #ffffff;
  line-height: 1.6;
  opacity: 0.9;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
}

@media (max-width: 768px) {
  .stats-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }
  
  .stat-card {
    padding: 30px 20px;
  }
  
  .stats-title {
    letter-spacing: 3px;
  }
}
